<template>
	<transition name="fade">
		<section class="pop" v-show="show">
		<h2>{{name}}</h2>
		<h3><span>优惠信息</span></h3>
		<ul>
			<li v-for="(item,index) in activity" :key="index"><span :style="{backgroundColor:'#'+activity[0].icon_color}">{{activity[0].icon_name}}</span>{{item.description}}</li>
		</ul>
		<h3><span>商家公告</span></h3>
		<div class="content">{{promotion}}</div>
		<div class="close" @click="show=false">
          <svg>
            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#res-x"></use>
          </svg>
    </div>
	</section>
	</transition>
</template>

<script>
  export default {
    props: {
      name: {
        type: String,
        required: true
      },
      activity: {
        type: Array,
        required: true
      },
      promotion: {
        type: String,
        required: true
      }
    },
    data () {
      return {
        show: false
      }
    },
    methods: {
      open () {
        this.show = true
      }
    }
}
</script>

<style lang="less" scoped>
  .fade-enter-active,.fade-leave-active{transition: all 1s;}
  .fade-enter,.fade-leave-to{ opacity: 0; }
	.pop{position: fixed;left:0px;right:0px;top:0px;bottom:0px;
	background-color:#262626;
	border:.8rem solid transparent;
	text-align:center;z-index:999;
	h2{font-size:1.5em}
	h3{
		margin:.8rem 0 .533333rem;
		span{
			font-size:.32rem;padding:.133333rem .266667rem;border-radius:.666667rem;
	border:1px solid #555;background-color:#262626;
	}
	}
	ul{
		text-align:left;
		li{
			margin-bottom:.2rem;font-size: .293333rem;
			span{
				margin-right: .16rem;
        font-size: .293333rem;
        width: .373333rem;
        height: .373333rem;
        line-height: .373333rem;
        text-align:  center;
        border-radius: .053333rem;
        display: inline-block;
			}
		}
	}
	.content{
		text-align:left;
		line-height:0.5rem;
	}
	.close{
		position: fixed;left:0px;right:0px;bottom: .666667rem;width:1.2rem;
		height:1.2rem;border:1px solid #888;border-radius:50%;margin:auto;
		svg{fill:#fff;width:100%;height:100%;transform:scale(.3)}
	}
	}
</style>


