import {getLoction} from '@/api/location'
import * as types from './mutations-type'

export default{
  fetchLoction ({commit}) {
    return new Promise((resolve, reject) => {
      getLoction().then((res) => {
        resolve(res)
        commit(types.GET_LOCATION, res.coords)
      }).catch(err => {
        reject(err)
      })
    })
  }
}



