<template>
	<section class="goodPop" v-show="isShow" @click="close">
		<div class="main" @click.stop="mine">
			<span class="image">
				<img :src="obj.image_path | goodImg" alt="" />
			</span>
			<section class="content">
				 <header>{{obj.name}}</header>
				 <p>月售{{obj.month_sales}}份 好评率是{{obj.satisfy_rate}}%</p>
				 <strong>￥{{obj.specfoods[0].price}}</strong>
				 <div class="small">
				 	<pop-small :food="obj"></pop-small>
				 </div>
			</section>
		</div>
	</section>
</template>

<script>
  import Small from '@/components/small/Small'
  export default {
    data () {
      return {
        isShow: false
      }
    },
    props: {
      obj: {
        type: Object,
        required: true
      }
    },
    methods: {
      show () {
        this.isShow = true
      },
      mine () {
        return false
      },
      close () {
        this.isShow = false
      }
    },
    filters: {
      goodImg: function (str) {
        let name = str.slice(0, 1) + '/' + str.slice(1, 3) + '/' + str.slice(3)
        let matchs = str.match(/png|jpeg|jpg$/)
        return 'https://fuss10.elemecdn.com/' + name + '.' + matchs[0] + '?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/'
      }
    },
    components: {
      'pop-small': Small
    }
  }
</script>

<style lang="less" scoped>
  .goodPop{
  	position: fixed;
  	left:0px;right:0px;
  	top:0px;bottom: 0px;
  	background-color:rgba(0,0,0,0.5);
  	display: flex;
  	justify-content: center;
  	align-items: center;
  	z-index: 1000;
  	.main{
  		margin: auto auto;
  		width:8.533333rem;
  		height:11.413333rem;
  		border-radius:.106667rem;
  		.image{
  			width:100%;
  			height: 8.533333rem;
  			display:block;
  			img{
  				width:100%;
  				height:100%;
  			}
  		}
  		.content{
  			padding: .32rem;
  			background-color: #fff;
  			position: relative;
  			header{
   					font-size: .426667rem;
  					color: #333;
  					margin-bottom: .16rem;
  			}
  			p{
  				font-size: .293333rem;
  			}
  			strong{
  				font-size: .426667rem;
  				font-weight: 700;
  				color: #ff461d;
  			}
  			.small{
  				position: absolute;
  				bottom: .1rem;
  				right: .16rem;
  			}
  		}
  	}
  }
</style>


