<template>
	<section class="restaurant">
		<div class="head">
			<h3>活动与服务</h3>
			<div class="server">
				<p class="first"><span :style="{'backgroundColor':'#'+restaurant.activities[0].icon_color}">{{restaurant.activities[0].icon_name}}</span>{{restaurant.activities[0].description}}</p>
				<p class="discount"><span :style="{'backgroundColor':'#'+restaurant.activities[1].icon_color}">{{restaurant.activities[1].icon_name}}</span>{{restaurant.activities[1].description}}</p>
				<p class="supports"><span v-if="restaurant.supports[0]">{{restaurant.supports[0].icon_name}}</span>{{restaurant.supports[0].description}}</p>
			</div>
		</div>
		<div class="shopImg">
			<h3>商家实景</h3>
			<div>
				<a href="" v-for="(item,index) in restaurant.albums">
					<img :src="item.cover_image_hash | restImg" alt="" />
					<span>{{item.name}}({{item.photos.length}}张)</span>
				</a>
			</div>
		</div>
		
		<div class="shopInfo">
			<section class="info">
				<h3>商家信息</h3>
				<ul>
					<li v-if="restaurant.description">{{restaurant.description}}</li>
					<li v-else style="color:#666">暂无简介</li>
					<li>
						<span>商家电话</span>
						<span class="right">
							<span>{{restaurant.phone}}</span>
							<span>></span>
						</span>
					</li>
					<li>
						<span>地址</span>
						<span class="address right">
							<span>{{restaurant.address}}</span>
						</span>
					</li>
					<li>
						<span>营业时间</span>
						<span class="right">
							<span>{{restaurant.opening_hours[0]}}</span>
						</span>
					</li>
				</ul>
			</section>
			<section class="zizhi">
				<a href="">
					<h3>营业资质</h3>
					<span>></span>
				</a>
			</section>
		</div>
	</section>
</template>

<script>
  export default {
    props: {
      restaurant: {
        type: Object,
        required: true
      }
    },
    filters: {
      restImg (url) {
        let name = url.slice(0, 1) + '/' + url.slice(1, 3) + '/' + url.slice(3)
        let matchs = url.match(/png|jpeg|jpg$/)
        return 'https://fuss10.elemecdn.com/' + name + '.' + matchs[0] + '?imageMogr/format/webp/thumbnail/!200x200r/gravity/Center/crop/200x200/'
      }
    }
}
</script>

<style lang="less" scoped>
	.restaurant{
		background-color: #f5f5f5;
		h3{
				color:#000;
				font-weight: 700;
				font-size: .4rem;
				line-height:.56rem;
			}
		.head{
			padding:.426667rem .4rem .4rem;background-color: #fff;
			color:#666;margin-bottom:.266667rem;
			.server{
				font-size: .293333rem;
				margin-top: .426667rem;
				p{
					margin-bottom:.186667rem;
					span{
						margin-right:.16rem;
						padding: .05rem;
					}
				}
				.first{
					span{
						display: inline-block;
						color:#fff;
					}
				}
				.discount{
					margin-bottom:.26rem;
					span{
						display: inline-block;
						font-size: .293333rem;
						color: #fff;
					}
				}
				.supports{
					span{
						border:1px solid;
						padding: .03rem;
						color:rgb(153, 153, 153);
					}
				}
			}
		}
		
		
		.shopImg{
			background-color: #fff;padding: .426667rem .4rem .4rem;
			margin-bottom: .266667rem;
			a{
				text-decoration: none;
				position: relative;
				width:2.08rem;
				height:2.08rem;
				display: inline-block;
				margin-right: .186667rem;
				img{
					width:100%;
					height:100%;
				}
				span{
					display: block;
					position: absolute;
					bottom:0px;
					width:100%;
					text-align: center;
					height:.48rem;
					line-height: .48rem;
					color:#eee;
					background-color: rgba(0,0,0,.6);
					font-size: .346667rem;
				}
			}
			div{
				margin-top:.213333rem;
			}
		}
		
		.shopInfo{
			font-size: .346667rem;
			.info{
				padding: .426667rem .4rem 0rem;
				background-color: #fff;
				margin-bottom: .266667rem;
				ul{
					li{
						padding: .4rem .4rem .4rem 0;
						border-bottom: 1px solid #DDDDDD;
						display: flex;
						justify-content: space-between;
						align-items: center;
						.address{
							text-align: right;
						}
						.right{
							max-width: 6.666667rem;
							text-align: right;
							color:#666;
							margin-right: -.4rem;
							line-height: 1.2;
						}
					}
				}
			}
			.zizhi{
				padding: .426667rem .4rem .4rem;
				background-color: #fff;
				a{
					text-decoration: none;
					display: flex;
					justify-content: space-between;
					align-items: center;
				}
			}
		}
	}
</style>


