<template>
	<section class="rating">
		<section class="head">
			<div class="head-left">
				<strong>{{restaurant.rating}}</strong>
				<p>综合评价</p>
				<p>高于周边商家71.7%</p>
			</div>
			<div class="head-right">
				<p>
					<span>服务态度</span>
					<svg>
						<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#star-actived">
						</use>
					</svg>
					<span class="total">4.7</span>
				</p>
				<p>
					<span >菜品评价</span>
					<svg>
						<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#star-actived">
						</use>
					</svg>
					<span class="total">4.5</span></p>
				<p><span>送达时间</span><span class="time">{{restaurant.order_lead_time}}分钟</span></p>
			</div>
		</section>
		
		<section class="main">
			<div class="tab">
				<ul>
					<li @click="current='all'" :class="{active:current=='all'}">全部({{rating.length}})</li>
					<li @click="current='agree'" :class="{active:current=='agree'}">满意(606)</li>
					<li class="no" @click="current='disagree'" :class="{active:current=='disagree'}">不满意(13)</li>
					<li @click="current='hasImage'" :class="{active:current=='hasImage'}">有图(11)</li>
				</ul>
			</div>
			<ul class="detailRating">
				<li v-for="(item,index) in show" :key="index">
					  <div class="ratinglist">
						<img src="https://fuss10.elemecdn.com/c/f5/d0b0f2fc83f3ac3e4a0cfae891256png.png?imageMogr/format/webp/thumbnail/!60x60r/gravity/Center/crop/60x60/" alt="" />
						<div class="info">
								<h3>{{item.username}}</h3>
								<small>{{item.rated_at}}</small>
						</div>
						<p class="star">
								<svg>
									<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#star-actived">
									</use>
					      </svg>
							</p>
							<div class="goodname">
								<ul>
									<li v-for="(foodname,index) in item.item_ratings">{{foodname.food_name}}</li>
								</ul>
							</div>
						</div>
				</li>
			</ul>
		</section>
	</section>
</template>

<script>
  export default {
    props: {
      rating: {
        type: Array,
        required: true
      },
      restaurant: {
        type: Object,
        required: true
      }
    },
    data () {
      return {
        current: 'all'
      }
    },
    computed: {
      show () {
        let showArr = []
        if (this.current === 'all') {
          showArr = this.rating
        } else if (this.current === 'agree') {
          showArr = this.rating.filter(item => {
            return item.rating_star >= 4
          })
        } else if (this.current === 'disagree') {
          showArr = this.rating.filter(item => {
            return item.rating_star < 4
          })
        } else {
          showArr = this.rating
        }
        return showArr
      }
    }
}
</script>

<style lang="less" scoped>
	.rating{
		background-color: #F5F5F5;
		.head{
			display: flex;
			font-size:.32rem;
			padding:.4rem;
			margin-bottom:.266667rem;
			background-color: #fff;
			color:#666;
			.head-left{
				padding-right:.4rem;
				text-align: center;
				border-right:1px solid #ddd;
				line-height: 1.3;
				strong{
					font-size: .64rem;
					color:#ff6000;
					font-weight:700;
					display: block;
				}
			}
			.head-right{
				padding-left:.8rem;
				p{
					margin:.233333rem 0;
					svg{
						width:1.493333rem;
						height:.266667rem;
					}
					.total{
						padding-left:.266667rem;
						color:#f60;
					}
					.time{
						padding-left: .266667rem;
					}
				}
			}
		}
		
		.main{
			background-color: #fff;
			padding: .266667rem .32rem 0;
			.tab{
				padding-bottom: .266667rem;
				border-bottom:1px solid #ddd;
				li{
					display: inline-block;
					padding:0 .24rem;
					height:.746667rem;
					line-height: .746667rem;
					margin: .093333rem;
					font-size:.32rem;
					border-radius:.053333rem;
					color:#6d7885;
					background-color:#ebf5ff;
					&.no{
						background-color: #F5F5F5;
						&.active{
							color:#fff;
							background-color: #6d7885;
						}
					}
					&.active{
						color:#fff;
						background-color: #0097ff;
					}
				}
			}
			.detailRating{
				line-height: 1;
				li{
					padding: .4rem 0;
					border-bottom:.013333rem solid #ddd;
					.ratinglist{
						padding-left:1.2rem;position:relative;
						img{
							position: absolute;
							top:0px;
							left:0px;
							width:.8rem;
							height:.8rem;
							border-radius: 50%;
						}
						.info{
							display: flex;align-items: center;
							h3{
								font-size: .346667rem;
								margin-top:0px;
								color:#333;
								margin-right:.16rem;
							}
							small{
								color:#bbb;
								font-size: .293333rem;
							}
						}
						.star{
							display: flex;
							align-items: center;
							margin-top:.1rem;
							svg{
								width:1.493333rem;
						    height:.266667rem;
							}
						}
						.goodname{
							li{
								display: inline-block;
								font-size: .293333rem;
								color: #6d7885;
								border:1px solid #eee;
								padding: .106667rem .266667rem;
								margin:.093333rem;
								border-radius: .053333rem;
								max-width: 2.6rem;
								white-space:nowrap;
								overflow: hidden;
								text-overflow:ellipsis;
							}
						}
					}
				}
			}
		}
	}
</style>


