<template>
	<section>
		<div class="foodwrap">
			<div class="menulist">
				<ul>
				<li v-for="(item,index) in shop" :key='index' :class="{active:index==current}" ref="menu" @click="changeContent(index)"><span>{{item.name}}</span></li>
			</ul>
			</div>
			<div class="foodlist">
				<div>
					<dl v-for="(item1,index1) in shop" ref="content">
					<dt><strong>{{item1.name}}</strong><span>{{item1.description}}</span></dt>
					<dd v-for="food in item1.foods" @click="pop(food.item_id)">
						<div class="wrap">
								<img :src="food.image_path | goodImg" alt="" v-if="food.image_path"/>
								<div class="food">
									<h4>{{food.name}}</h4>
			            <p class="desc">{{food.description}}</p>
			            <p class="sale">月售{{food.month_sales}}份 好评率是{{food.satisfy_rate}}%</p>
			            <p class="price">
		              <strong><span>￥{{food.specfoods[0].price}}</span></strong>
		              <del v-if="food.specfoods[0].oldPrice">￥{{food.specfoods[0].oldPrice}}</del>
		            </p>
								</div>
								<goods-small :food='food'></goods-small>
						</div>
					</dd>
				</dl>
				</div>
			</div>
			<goods-pop ref="pop" :obj="obj"></goods-pop>
			<shop-cart :shop='shop' :fee='restaurant.float_delivery_fee' :minimum="restaurant.float_minimum_order_amount"></shop-cart>
		</div>
	</section>
</template>

<script>
import IScroll from '../../../static/iscroll-probe'
import Small from '@/components/small/Small'
import GoodsPop from './GoodsPop'
import ShopCart from './ShopCart'
export default {
  data () {
    return {
      current: 0,
      distance: [],
      obj: {}
    }
  },
  components: {
    'goods-small': Small,
    'goods-pop': GoodsPop,
    'shop-cart': ShopCart
  },
  methods: {
    changeContent (index) {
      this.current = index
      this.myscroll.scrollToElement(this.$refs.content[index])
    },
    getEvery () {
      this.distance.push(0)
      for (let i = 0; i < this.$refs.content.length; i++) {
        this.distance.push(this.distance[i] + this.$refs.content[i].clientHeight)
      }
    },
    pop (id) {
      this.shop.forEach(item => {
        item.foods.forEach(food => {
          if (food.item_id === id) {
            this.obj = food
          }
        })
      })
      this.$refs.pop.show()
    }
  },
  props: {
    shop: {
      type: Array,
      required: true
    },
    restaurant: {
      type: Object,
      required: true
    }
  },
  filters: {
    goodImg: function (str) {
      let name = str.slice(0, 1) + '/' + str.slice(1, 3) + '/' + str.slice(3)
      let matchs = str.match(/png|jpeg|jpg$/)
      return 'https://fuss10.elemecdn.com/' + name + '.' + matchs[0] + '?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/'
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.myscroll = new IScroll('.foodlist', {probeType: 2})
      this.menuscroll = new IScroll('.menulist', {probeType: 2})
      let that = this
      this.myscroll.on('scroll', function () {
        let dis = Math.abs(this.y)
        for (let i = 0; i < 18; i++) {
//        console.log(that.distance.length)
          let start = that.distance[i]
          let end = that.distance[i + 1]
          if (dis >= start && dis <= end) {
            console.log(i)
            that.current = i
//          that.menuscroll.scrollToElement(this.$refs.menu[i])
          }
        }
      })
    })
  },
  updated () {
    this.myscroll.refresh()
    this.getEvery()
//  console.log(this.distance)
  }
}
</script>

<style lang="less" scoped>
		.foodwrap{
			display:flex;position: absolute;
			left:0;right:0;top: 4.812667rem;
			bottom: 1.28rem;overflow: hidden;
		  ul{
		  	background:#f8f8f8;width:2.266667rem;
			  li{font-size: .346667rem!important;
			  color: #666;
			  padding: .466667rem .2rem;
			  &.active{
			  	background-color: #fff;
			  }
			  }
		  }
		  .foodlist{width:7.733333rem;
		  	dl{
		  		flex:none;
		  		dt{
		  			padding:.2rem .8rem .2rem .266667rem;
		  			background-color:#f1f1f1;height: .84rem;
		  			display: flex;align-items: center;
		  			strong{
		  				font-size: .373333rem;
		  				font-weight: 700;
		  				margin-right:.133333rem;
		  			}
		  			span{
		  				font-size: .33rem;
		  				color:#999;
		  				white-space:nowrap;
		  				overflow: hidden;
		  				text-overflow:ellipsis;
		  			}
		  		}
		  		dd{
		  			.wrap{
		  				position: relative;
		  				display: flex;
		  				padding:.4rem .266667rem;
		  			min-height:3.066667rem;
				  	.small{
				  		
				  	}
		  			img{
		  				width:2rem;
		  				height:2rem;
		  				margin-right:.2rem;
		  				flex:0 0 auto;
		  			}
		  			.food{
		  				flex:1;
		  				h4{
		  					font-size: .4rem;
		  					font-weight: 700;
		  					line-height: 1.1;
		  					overflow: hidden;
			  				text-overflow:ellipsis;
		  				}
		  				.sale{
		  					font-size: .293333rem;
		  					color:#666;
		  					line-height: 1.1;
		  					margin: .173333rem 0!important;
		  					
		  				}
		  				.desc{
		  					font-size: .253333rem;
		  					margin:.133333rem 0!important;
		  					color:#999;
			  				overflow: hidden;
			  				text-overflow:ellipsis;
		  				}
		  				.price{
		  					position:absolute;
		  					bottom: 0;
		  					strong{
		  						span{
		  							font-size: .426667rem;
		  							color: #f60;
		  						}
		  					}
		  				}
		  				strong{
		  					margin-top:.133333rem;
		  				}
		  			}
		  			}
		  			
		  		}
		  	}
		  }
		}
</style>


