<template>
	<header>
			<section class="header-bg" :style="backg">
      </section>
      <nav>
      	<svg @click="$router.go(-1)">
			  <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-left.6f6409e" ></use>
			</svg>
      </nav>
			
			 <section class="main">
        <img :src="restaurant.image_path | goodImg" alt="" v-if="restaurant.image_path">
        <div>
          <h2>{{restaurant.name}}</h2>
          <p class="delivery">
            <span v-if="restaurant.delivery_mode">{{restaurant.delivery_mode.text}}</span>
            <span v-else>商家配送</span> /
            <em>{{restaurant.order_lead_time}}分钟送达</em> /
            <strong>配送费￥{{restaurant.float_delivery_fee}}</strong>
          </p>
          <p class="notice">
            <span v-if="restaurant.promotion_info">公告：</span>
            <span>{{restaurant.promotion_info}}</span>
          </p>
        </div>
      </section>
      <section class="activity" @click="$refs.pop.open()">
        <div>
          <strong :style="{backgroundColor:'#'+restaurant.activities[0].icon_color}" v-if="restaurant.activities">{{restaurant.activities[0].icon_name}}</strong>
          <span v-if="restaurant.activities">{{restaurant.activities[0].description}}</span>
        </div>
        <aside>
          {{restaurant.activities.length}}个活动
        </aside>
      </section>
      <shop-pop :name="restaurant.name" :activity='restaurant.activities' :promotion='restaurant.promotion_info' ref="pop"></shop-pop>
	</header>
</template>

<script>
	import Pop from './Pop'
export default {
  props: {
    restaurant: {
      type: Object,
      require: true
    }
  },
  data () {
    return {
      bgurl: '',
      backg: {
        'backgroundImage': 'url(' + this.bgurl + ')'
      }
    }
  },
  updated () {
    console.log(this.restaurant.image_path)
    this.bgurl = this.restaurant && this.bgUrl(this.restaurant.image_path)
  },
  methods: {
    bgUrl (str) {
      console.log(this.restaurant.image_path)
      let name = str.slice(0, 1) + '/' + str.slice(1, 3) + '/' + str.slice(3)
      let matchs = str.match(/png|jpeg|jpg$/)
      return 'https://fuss10.elemecdn.com/' + name + '.' + matchs[0] + '?imageMogr/format/webp/thumbnail/!40p/blur/50x40/'
    }
  },
  filters: {
    goodImg: function (str) {
      let name = str.slice(0, 1) + '/' + str.slice(1, 3) + '/' + str.slice(3)
      let matchs = str.match(/png|jpeg|jpg$/)
      return 'https://fuss10.elemecdn.com/' + name + '.' + matchs[0] + '?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/'
    }
  },
  components: {
    'shop-pop': Pop
  }
}
</script>

<style lang="less" scoped>
 header {
    position: relative;
    padding-bottom: .8rem;
    color: #fff;
    font-size: .293333rem;
    .header-bg {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: #3190e8;
      background-size: cover;
      background-repeat: no-repeat;
      z-index: -1;
      &::before {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-color: rgba(119,103,137,.43);
      }
    }
   
    nav {
      position: relative;
      padding: .106667rem .266667rem;
       svg {
        width: .666667rem;
        height:.666667rem;
      }
    }
    .main {
      padding: .266667rem .4rem 0;
      display: flex;
      img {
        width: 1.733333rem;
        height: 1.733333rem;
        border: 1px solid #fff;
        border-radius: .053333rem;
        margin: 0 .266667rem 0 0;
      }
      div {
        flex: 1;
        width: 7.2rem;
        h2 {
          font-size: .466667rem;
          line-height: 1.2em;
          font-weight: 700;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        p{
          height: .666667rem;
          line-height: .666667rem;
          white-space: nowrap;
        }
        .notice {
          height: .533333rem;
          line-height: .533333rem;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
    .activity{
      position: absolute;
      left: .4rem;
      right: .4rem;
      bottom:.133333rem;
      strong {
        margin-right: .16rem;
        font-size: .293333rem;
        width: .373333rem;
        height: .373333rem;
        line-height: .373333rem;
        text-align:  center;
        border-radius: .053333rem;
        display: inline-block;
      }
      aside {
        position: absolute;
        top: .066667rem;
        right: 0;
        &::after {
          content: '';
          display: block;
          border-width: .066667rem 0 .066667rem .08rem;
          border-style: solid;
          border-color: transparent transparent transparent #fff;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          right: -.16rem;
        }
      }
    }
  }
</style>


