<template>
	<header>
		<div class="head">
			<svg :v-show="showSvg" @click="back">
			  <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-left.6f6409e" ></use>
			</svg>
			<h1>{{title}}</h1>
		</div>
	</header>
</template>

<script>
  export default{
    props: {
      showSvg: {
        type: Boolean
      },
      title: {
        type: String
      }
    },
    methods: {
      back: function () {
        this.$emit('goback')
      }
    }
  }
</script>

<style lang="less" scoped>
   header{
	height:1.173333rem;background-image:linear-gradient(90deg,#0af,#0085ff);
	width:100%;padding: ;
   	.head{
   		position:fixed;top:0;left:0;width:100%;bottom:0;height:1.173333rem;
   		display:flex;align-items: center;justify-content: space-between;padding-left:0.2rem;
   		svg{
   			display:block;width:.586667rem;
   			height:.586667rem;fill:#fff;
   		}
   		h1{
   			color:#fff;
   			font-size: 0.5em;
   			font-weight:700;
   			position: absolute;
   			top:0;
   			left:50%;height:100%;
   			line-height: 1.173333rem;
   			transform:translateX(-50%);
   		}
   	}
   }
</style>


