<template>
 <section class="small">
 		<div class="left" @click.stop="sub(food)" v-if="food.count">
 			<svg>
 				<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#cart-add"></use>
 			</svg>
 		</div>
 		<div class="count" v-if="food.count">{{food.count}}</div>
 		<div class="right"  @click.stop="add(food)">
 			<svg>
 				<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#cart-minus"></use>
 			</svg>
 		</div>
 </section>
</template>

<script>
  export default {
    props: {
      food: {
        type: Object,
        required: true
      }
    },
    methods: {
      add (food) {
        if (food.count) {
          food.count++
        } else {
          this.$set(food, 'count', 1)
        }
      },
      sub (food) {
        if (food.count > 0) {
          food.count--
        }
      }
    }
  }
</script>

<style lang="less" scoped>
		.small{
			/*position: relative;*/
			width:160px;
			font-size: .346667rem;
			height:.7rem;
			position: absolute;
			right:.266667rem;
			bottom:0rem;
			svg{
				width: 40px;
      height: 40px;
      fill: #3190e8;
			}
			div{
				position: absolute;
				top:0px;
			}
			.left{
				left:10px;
			}
			.right{
				right:10px;
			}
			.count{
				left:50%;
				transform: translate(-50%);
				line-height: 40px;
			}
		}
</style>


