<script>
	import Footer from '@/components/footer/Footer'
	import { Swipe, SwipeItem } from 'mint-ui'
	import 'mint-ui/lib/style.css'
	import Vue from 'vue'
	Vue.component(Swipe.name, Swipe)
	Vue.component(SwipeItem.name, SwipeItem)
import _ from 'lodash'
	import HandInput from './HandInput'
	import { getAddress } from '@/api/location'
import { getHot, getEntry, getRest } from '@/api/msite'
// import { getWeather } from '@/api/msite'
	import { mapActions, mapState } from 'vuex'
export default {
  data: function () {
    return {
      address: {
        code: 1,
        msg: '正在定位中...'},
      weather: null,
      hot: [],
      entry: [],
      page: 1,
      limit: 20,
      offset: 0,
      rest: [],
      hasCompleted: false,
      sTop: ''
//    longitude: 116.3579348,
//    latitude: 40.1144418
    }
  },
  components: {
    'ele-footer': Footer,
    'hand-input': HandInput
  },
  methods: {
    ...mapActions(['fetchLoction']),
    msiteopen: function () {
      this.$refs.address.open()
    },
    enterShop: function (id) {
      this.$router.push({path: '/shop', query: {id}})
    }
  },
  computed: {
    ...mapState(['longitude', 'latitude'])

  },
  created () {
    this.fetchLoction().then((res) => {
      getAddress(this.latitude, this.longitude).then((pos) => {
        this.address = {
          code: 0,
          msg: pos.data.address
        }
      }).then(() => {
        getHot(this.latitude, this.longitude).then((hot) => {
          this.hot = hot.data
        }).catch(err => console.log(err))
      }).then(() => {
        getEntry(this.latitude, this.longitude).then((entries) => {
          this.entry = _.chunk(entries.data[0].entries, 8)
        }).catch(err => {
          console.log(err)
        })
      }).then(() => {
        this.offset = (this.page - 1) * this.limit
        getRest(this.latitude, this.longitude, this.offset, this.limit).then(res => {
          if (res.data.length > 0) {
            res.data.forEach(item => {
              this.rest.push(item)
            })
            this.page++
          } else {
            this.hasCompleted = true
          }
        }).catch(err => {
          console.log(err)
        })
      }).catch(err => {
        console.log(err)
        this.address = {
          code: 2,
          msg: '不能获取定位'
        }
      })
    }).catch(err => {
      console.log(err)
      this.address = {
        code: 3,
        msg: '不能获取定位'
      }
    })
  },
  mounted () {
    this.$nextTick(() => {
      window.addEventListener('scroll', () => {
        this.offset = (this.page - 1) * this.limit
        console.log(this.offset, this.page)
        if (document.body.scrollTop + window.innerHeight >= document.body.offsetHeight - 200) {
          if (this.hasCompleted === false) {
            getRest(this.latitude, this.longitude, this.offset, this.limit).then((res) => {
              if (res.data.length > 0) {
                res.data.forEach((val, index) => {
                  this.rest.push(val)
                })
                this.page++
              } else {
                this.hasCompleted = true
              }
            }).catch((error) => {
              console.log(error)
            })
          }
        }
      })
    })
  },
  filters: {
    dealUrl: function (str) {
      let name = str.slice(0, 1) + '/' + str.slice(1, 3) + '/' + str.slice(3)
      let matchs = str.match(/png|jpeg|jpg$/)
      return 'https://fuss10.elemecdn.com/' + name + '.' + matchs[0] + '?imageMogr/format/webp/thumbnail/!69x69r/gravity/Center/crop/69x69/'
    },
    dealUrl2: function (str) {
      let name = str.slice(0, 1) + '/' + str.slice(1, 3) + '/' + str.slice(3)
      let matchs = str.match(/png|jpeg|jpg$/)
      return 'https://fuss10.elemecdn.com/' + name + '.' + matchs[0] + '?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/'
    },
    getKm: function (dis) {
      let distance = (dis / 1000).toFixed(2)
      return parseFloat(distance)
    }
  }
}
</script>

