<template>
  <section>
  	<shop-header :restaurant="restaurant"></shop-header>
  	<ul>
  		<li @click="current='shop-goods'" :class="{active:current=='shop-goods'}"><span>商品</span></li>
  		<li @click="current='shop-rating'" :class="{active:current=='shop-rating'}"><span>评价</span></li>
  		<li @click="current='shop-rest'" :class="{active:current=='shop-rest'}"><span>商铺</span></li>
  	</ul>
  	<component :is="current" :shop="shop" :rating="rating" :restaurant="restaurant"></component>
  	<shop-cart :shop='shop' :fee='restaurant.float_delivery_fee' :minimum="restaurant.float_minimum_order_amount" v-show="current=='shop-goods'"></shop-cart>
  </section>
</template>

<script>
	import { getShop, getRating, getRest } from '@/api/shop'
	import ShopHeader from './ShopHeader'
	import Goods from './Goods'
	import Rating from './Rating'
	import Rest from './Rest'
import ShopCart from './ShopCart'
export default {
  data () {
    return {
      shop: [],
      rating: [],
      restaurant: {},
      current: 'shop-goods',
      page: 1,
      limit: 10,
      hasContent: true
    }
  },
  mounted () {
    let id = this.$route.query.id
//  console.log(id)
    getShop(id).then(res => {
      console.log(res.data)
      this.shop = res.data
    }).catch(err => {
      console.log(err)
    })
    let offset = (this.page - 1) * this.limit
    getRating(id, this.hasContent, offset, this.limit).then((res) => {
      this.rating = res.data
    }).catch(err => {
      console.log(err)
    })
    getRest(id).then(res => {
//    console.log(res.data)
      this.restaurant = res.data
    }).catch(err => {
      console.log(err)
    })
  },
  components: {
    'shop-header': ShopHeader,
    'shop-goods': Goods,
    'shop-rating': Rating,
    'shop-rest': Rest,
    'shop-cart': ShopCart
  }
}
</script>

<style lang="less" scoped>
  section{
  	ul{
  		display: flex;
  		justify-content: space-around;
  		align-items: center;
  		font-size: 30px;
  		line-height:1.066667rem;
  		background-color:#fff;
  		border-bottom:1px solid #eee;
  		li.active{
  			color:#666;
  			font-size: .373333rem;
  			text-align: center;
  			position: relative;
  			span{
  				color:#3190e8;
  				font-weight:700;
  				&::after {
              content: "";
              position: absolute;
              left: 0;
              right: 0;
              height: .08rem;
              bottom: 0.05rem;
              background: #2395ff;
            }
  			}
  		}
  	}
  }
</style>


